import React, { Component } from 'react';
import { connect } from 'react-redux';
import {deleteMemo} from './Store';

class DelForm extends Component {
    input = {
        fontSize: "12pt",
        padding: " 1px",
        margin: "5px 0",
        color: "#006",
        border: "2px solid #006",
    }
    btn = {
        fontSize: "10pt",
        padding: "2px 10px",
        color: "#006",
        border: "2px solid #006",
        marginLeft:"10px"
    }

    constructor(props) {
        super(props);
        this.state = {
            number: 0
        }
        this.doChange = this.doChange.bind(this);
        this.doAction = this.doAction.bind(this);
    }

    doChange(e) {
        this.setState({
            number: e.target.value
        });
    }
    doAction(e) {
        e.preventDefault();
        let action = deleteMemo(this.state.number);
        this.props.dispatch(action);
        this.setState({
            message: 0
        });
    }

    render() {
        let n = 0;
        let items = this.props.data.map((value) => (
            <option value={n++} key={n}>{value.message.substring(0, 10)}</option>
        ));
        return (
            <div>
                <form onSubmit={this.doAction}>
                    <select onChange={this.doChange} style={this.input} defaultValue="-1">
                        {items}
                    </select>
                    <input type="submit" style={this.btn} value="Del" />
                </form>
            </div>
        );
    }
}

export default connect((state) => state)(DelForm);