import React, { Component } from 'react';
import { connect } from 'react-redux';
import {findMemo} from './Store';

class FindForm extends Component {
    input = {
        fontSize: "14pt",
        padding: " 1px",
        margin: "5px 0",
        color: "#006",
        border: "2px solid #006",
    }
    btn = {
        fontSize: "12pt",
        padding: "2px 10px",
        color: "#006",
        border: "2px solid #006",
        marginLeft: "10px"
    }

    constructor(props) {
        super(props);
        this.state = {
            find: ''
        }
        this.doChange = this.doChange.bind(this);
        this.doAction = this.doAction.bind(this);
    }

    doChange(e) {
        this.setState({
            find: e.target.value
        });
    }
    doAction(e) {
        e.preventDefault();
        let action = findMemo(this.state.find);
        this.props.dispatch(action);
        this.setState({
            find: ''
        });
    }

    render() {
        return (
            <div>
                <form onSubmit={this.doAction}>
                    <input type="text" onChange={this.doChange} size="10" style={this.input} value={this.state.message}/>
                    <input type="submit" style={this.btn} value="Find" />
                </form>
            </div>
        );
    }
}

export default connect((state) => state)(FindForm);